.rmdb-header {
    width: 100%;
    height: auto;
    background: #1c1c1c;
    padding: 0 20px;
    box-sizing: border-box;
  }
  
  .rmdb-header-content {
    max-width: 1280px;
    min-height: 120px;
    height: auto;
    padding: 20px 0px;
    margin: 0 auto;
    box-sizing: border-box;
    overflow: hidden;
  }
  
  .rmdb-logo {
    width: 300px;
    margin-top: 20px;
    float: left;
  }
  
  .rmdb-tmdb-logo {
    width: 122px;
    margin-top: 25px;
    float: right;
  }
  
  @media screen and (max-width: 500px) {
    .rmdb-header-content {
      max-width: 1280px;
      min-height: 0px;
    }
  
    .rmdb-tmdb-logo {
      display: inline-block;
      width:80px;
      margin-top: 0px;
    }
  
    .rmdb-logo {
      width: 150px;
      margin-top: 5px;
    }
  }
.rmdb-heroimage {
  background-size: 100%, cover !important;
  background-position: center, center !important;
  width: 100%;
  height: 600px;
  position: relative;
  -webkit-animation: animateHeroimage 1s;
          animation: animateHeroimage 1s;
}

@-webkit-keyframes animateHeroimage {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@keyframes animateHeroimage {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

.rmdb-heroimage-content {
  max-width: 1280px;
  padding: 20px;
  margin: 0 auto;
}

.rmdb-heroimage-text {
  z-index: 100;
  max-width: 700px;
  position: absolute;
  bottom: 40px;
  margin-right: 20px;
  min-height: 100px;
  background: rgba(0, 0, 0, 0.0);
  color: #fff;
}

.rmdb-heroimage-text h1 {
  font-family: 'Abel', sans-serif;
  font-size:48px;
  color: #fff;
}

.rmdb-heroimage-text p {
  font-family: 'Abel', sans-serif;
  font-size: 22px;
  line-height: 26px;
  color: #fff;
}

@media screen and (max-width: 720px) {
  .rmdb-heroimage-text {
    max-width: 100%;
  }

  .rmdb-heroimage-text h1 {
    font-size: 38px;
    color: #fff;
  }

  .rmdb-heroimage-text p {
    font-size: 16px;
    line-height: 20px;
    color: #fff;
  }
}
.rmdb-searchbar {
  width: 100%;
  height: 105px;
  background: #1c1c1c;
  position: relative;
  padding: 25px 20px 0px 20px;
  box-sizing: border-box;
  color: #fff;
}

.rmdb-searchbar-content {
  max-width: 1280px;
  width: 100%;
  height: 55px;
  background:#353535;
  margin: 0 auto;
  border-radius: 40px;
  position: relative;
